.ImageGallery {
  overflow: hidden;
  background-color: rgba(0,0,0,.9);
  border-radius: 2px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.ImageGallery__Wrapper {
  position: relative;
	width: 100%;
	margin: 0;
	padding: 0;
	font-size: 0;
	text-align: left;
}

.ImageGallery__Image {
  width: 100%;
  height: auto;
	float: left;
  display: none;
  -webkit-transition: opacity 1s;
  -o-transition: opacity 1s;
  transition: opacity 1s;
}

.ImageGallery__Image--transition {
  opacity: 0;
  display: block;
  position: absolute;
}

.ImageGallery__Image--active {
  opacity: 1;
  display: block;
}



