.PageBody {
  width: 80%;
  background-color: rgba(0,0,0,.9);
  border-radius: 2px;
  padding: 2rem;
  display: -ms-flexbox;
  display: flex;
  color: white;
}

.PageBody__Description {
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-preferred-size: 50%;
      flex-basis: 50%;
  max-height: 37vh;
  overflow: auto;
  padding-right: 2rem;
}

.PageBody__Gallery {
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-preferred-size: 50%;
      flex-basis: 50%;
}

.PageBody__Title {
  display: inline-block;
  margin: 0;
  line-height: 1;
}



